@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Montserrat:wght@500;700&family=Roboto&family=Rubik+Moonrocks&display=swap");

@layer base {
  body {
    @apply font-[Roboto];
  }
  li {
    @apply px-4;
    @apply cursor-pointer;
  }
  .content-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
  }

  .content-div:hover {
    background-image: linear-gradient(to right, #0a192f, #081425) !important;
  }

  .list {
    list-style-type: disc;
    margin-left: 20px;
    margin-top: 20px;
  }
  .sql {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
